import $ from 'jquery';
import AutoSuggest from './autosuggest';
import Form from './form';

let $document = $(document),
	$body = $("body"),
	_forms = {};

export let currentUrl = new URL(window.location);
export { AutoSuggest, Form };

// Forms object/function
export function forms(el) { // id or element
	var id = ($.isString(el)) ? el : getForm(el).id;
	return _forms[id];
}

// Add form
export function addForm(f) {
	_forms[f.id] = f;
}

// Remove spinner (immediately)
var _removeSpinner = ew.removeSpinner;

// Remove spinner
export function removeSpinner() {
	var timer = $body.data("_spinner");
	if (timer)
		timer.cancel();
	timer = $.later(500, null, function() {
		if ($document.data("_ajax") !== true) // Ajax not running
			_removeSpinner();
	});
	$body.data("_spinner", timer);
}

// Init icon tooltip
